  }
  

  .ml-button-disabled{
    box-sizing: content-box;
    font-family: 'Noto Sans', sans-serif;
    font-size: 14px;
    text-transform: uppercase;
    background-color: transparent;
    text-decoration: none;
    font-weight: bold;
    border-radius: 2px;
    padding: 12px 14px;
    border: 0;
    display: inline-block;
  }
